import React, { useState } from 'react';
import { Modal, Alert } from 'react-bootstrap';

import Button from '../Button';
import ToggleSection from '../ToggleSection';



import * as styles from './FlashMessageModal.module.scss';


function FlashMessageModal() {
  const [showMessage, setShowMessage] = useState(true);

  const handleClose = () => setShowMessage(false);

  return (
    <Modal show={showMessage} onHide={handleClose} backdrop="static" keyboard={false}>
      <Modal.Header className={styles.header}>
        <Modal.Title>Cookie </Modal.Title>
      </Modal.Header>
      <Modal.Body className={styles.body}>
        <div className={styles.sentence}>
          Cookie
          Cookie Cookie /

        </div>
        
        <div className={styles.okButtonsContainer}>
          <Button onClick={handleClose} level={'levelGreen'} size={'thin'}></Button>
          <Button onClick={handleClose} level={'levelBlack'} size={'thin'}></Button>
        </div>
      </Modal.Body>

      <Modal.Footer>
      </Modal.Footer>
    </Modal>
  );
}

export default FlashMessageModal;
